import BaseApiService from "services/BaseApiService";
const DEFAULT_PATH = `/custom/api/loan/query`;

class CreditQueryService extends BaseApiService {
  getAll = (start = 0, limit = 10, search: string) => {
    const url = `${DEFAULT_PATH}/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };

  updateQueryResponse = (payload: any) => {
    const url = `${DEFAULT_PATH}/${payload.uid}`;
    return this.makePutRequestWithAuth(url, payload);
  };
}

export default new CreditQueryService();
